import React, { useContext, useState,useEffect } from "react";
import "./Contact.css";
import { themeContext } from "../../Context";
import Aos from "aos";


const Contact = () => {
  const theme = useContext(themeContext);
  const darkMode = theme.state.darkMode;

  const [name, setName] = useState(" ")
  const [email, setEmail] = useState(" ")
  const [massage, setMassage] = useState(" ")
  const [done, setDone] = useState(false)
  const submitData = () => {
    if (name !== ' ' && email !== ' ' && massage !== ' ') {
      setDone(true);
    }
  };

  useEffect(() => {    
    Aos.init({duration:1200, offset:400});
  }, [])
  return (
    <div className="contact-form" id="contact"  data-aos="fade-up-right">
      <div className="contact-name">
          <span style={{ color: darkMode ? 'var(--orange)' : 'var(--orange)' }}>Contact</span>
          <div
            className="blur s-blur1"
            style={{ background: "#ABF1FF94" }}
          ></div>
        </div>
      
      {/* right side form */}
      <div className="c-right">
        <div className="form">
          <input type="text" className="user" onChange={(e) => setName(e.target.value)} placeholder="Name" />
          <input type="email" className="user" onChange={(e) => setEmail(e.target.value)} placeholder="Email" />
          <textarea name="message" className="user" onChange={(e) => setMassage(e.target.value)} placeholder="Message" />
          <button className="button" onClick={submitData}>Submit</button>
          <span style={{ color: "green" }}>{done && "Thanks for Contacting me"}</span>
          <div
            className="blur c-blur1"
            style={{ background: "var(--purple)" }}
          ></div>
        </div>
      </div>
    </div>
  );
};

export default Contact;