import React, { useContext } from "react";
import "./Intro.css";
import { themeContext } from "../../Context";
import { Link } from "react-scroll";
import Typewriter from "typewriter-effect";
import Aos from "aos";

// images import
import Vector1 from "../img/Vector1.png";
import Vector2 from "../img/Vector2.png";
import boy from "../img/boy.png";
import { useEffect } from "react";


const Intro = () => {
  // context
  const theme = useContext(themeContext);
  const darkMode = theme.state.darkMode;
  useEffect(()=>{
    Aos.init({duration:1700,offset:250})
  },[])


  return (
    <div className="Intro" id="Intro">
      {/* left name side */}
      <div className="i-left">
        <div className="i-name">
          <span style={{ color: darkMode ? "white" : "black" }}>Hy! I Am</span>
         <Typewriter
            options={{
              strings: ['Vikash Saini'],
              autoStart: true,
              loop: true,
            }}
          />
          <span style={{ color: darkMode ? "white" : "black" }}>
            Full Stack Developer ( MERN ) with high level of coding in web designing
            and development, producting the Quality work
          </span>
        </div>
        <Link to="contact" >
          <button className="button i-button">Hire me</button>
        </Link>

        {/* social icons */}
        <div className="i-icons" data-aos="fade-up-right">
          <a href="https://github.com/VikashSaini-RKD?tab=repositories" style={{ color: darkMode ? "white" : "black" }}><i className="fa-brands fa-github"  ></i></a>
          <a href="https://www.linkedin.com/in/vikash-saini-771879290/"><i className="fa-brands fa-linkedin" style={{ color: "blue" }} ></i></a>
          <a href='https://www.instagram.com/laadla_khatu_wale._.ka/'> <i className="fa-brands fa-instagram" style={{ color: "red" }} ></i></a>
          <a href='https://vikashsaini-rkd.github.io/vikas/' ><i className="fa fa-globe" ></i></a>
        </div>
      </div>

      {/* right image side */}
      <div className="i-right" data-aos="fade-up-left">
        <img src={Vector1} alt="" />
        <img src={Vector2} alt="" />
        <img src={boy} alt="" />


        <div className="blur" style={{ background: "rgb(238 210 255)" }}></div>
        <div
          className="blur"
          style={{
            background: "#C1F5FF",
            top: "17rem",
            width: "21rem",
            height: "11rem",
            left: "-9rem",
          }}
        ></div>
      </div>
    </div>
  );
};

export default Intro;