import React, { useContext, useEffect } from "react";
import { themeContext } from "../../Context";
import "./Services.css";
import Card from "../Card/Card";
import HeartEmoji from "../img/design.jpeg";
import Glasses from "../img/frontend.jpeg";
import Humble from "../img/backend.jpeg";
import { motion } from "framer-motion";
import Resume from '../img/Vikash.pdf';
import Typewriter from "typewriter-effect";
import Aos from "aos";

const Services = () => {
  const [html, setHtml] = React.useState(0)
  const [css, setCss] = React.useState(0)
  const [js, setJs] = React.useState(0)
  const [react, setReact] = React.useState(0)
  const [nodejs, setNodejs] = React.useState(0)
  const [express, setExpress] = React.useState(0)
  const [mongodb, setMongoDb] = React.useState(0)
  
  useEffect(() => {
    if (html < 90 ) {
      setTimeout(() => setHtml(html => html += 1),70 )
    }     
    Aos.init({duration:900, offset:250});
  }, [html])
  useEffect(() => {
    if (css < 85 ) {
      setTimeout(() => setCss(css => css += 1),80 )
    }     
  }, [css])
  useEffect(() => {
    if (js < 90 ) {
      setTimeout(() => setJs(js => js += 1),90 )
    }     
  }, [js])
  useEffect(() => {
    if (react < 92 ) {
      setTimeout(() => setReact(react => react += 1),80 )
    }     
  }, [react])
  useEffect(() => {
    if (nodejs < 80 ) {
      setTimeout(() => setNodejs(nodejs => nodejs += 1),90 )
    }     
  }, [nodejs])
  useEffect(() => {
    if (express < 80 ) {
      setTimeout(() => setExpress(express => express += 1),100 )
    }     
  }, [express])
  useEffect(() => {
    if ( mongodb< 85 ) {
      setTimeout(() => setMongoDb(mongodb => mongodb += 1),100 )
    }     
  }, [mongodb])

  // context
  const theme = useContext(themeContext);
  const darkMode = theme.state.darkMode;

  // transition
  const transition = { duration: 1, type: "spring", };

  return (
    <div className="services" id="services" data-aos="fade-right" >
      {/* left side */}
      <div className="awesome" >
        {/* dark mode */}
        <span style={{ color: darkMode ? "white" : "black" }}>My Awesome</span>
        <span>
          <Typewriter
            options={{
              strings: ['Services'],
              autoStart: true,
              loop: true,
            }}
          /></span>
        <p style={{ color: darkMode ? "white" : "gray" }}>
          Here All services avaliable Web designing, figma, sketch, photoshop
          <br />
          <br />
          <span>Full Stack Web Developer</span>
          <br />
          HTML, CSS, JAVASCRIPT, REACT JS,
          <br />
          NODE JS, EXPRESS JS, MONGODB
        </p>
        <a href={Resume} download data-aos="fade-right">
          <button className="button s-button">Download CV</button>
        </a>
        <div className="blur s-blur1" style={{ background: "#ABF1FF94" }}></div>
      </div>
      <div className="progressBar"  data-aos="fade-right">
        <span className="progressName">HTML</span>
        <div className='progress'  >
             <div className='pro-fill' style={{ width: `${html}%` ,backgroundColor:"red"}}>{html}%</div> 
        </div>
        <span className="progressName"  >CSS</span>
        <div className='progress' >
           <div className='pro-fill' style={{ width: `${css}%`  }}>{css}%</div> 
        </div>
        <span className="progressName"  >JAVASCRIPT</span>
        <div className='progress'>
           <div className='pro-fill' style={{ width: `${js}%`,backgroundColor:"yellow"  }}>{js}%</div> 
        </div>
        <span className="progressName" >REACT</span>
        <div className='progress' d>
        <div className='pro-fill' style={{ width: `${react}%` }}>{react}%</div> 
        </div>
        <span className="progressName">NODE JS</span>
        <div className='progress' >
           <div className='pro-fill' style={{ width: `${nodejs}%` ,backgroundColor:"lightgreen" }}>{nodejs}%</div> 
        </div>
        <span className="progressName">EXPRESS JS</span>
        <div className='progress'  >
          <div className='pro-fill' style={{ width: `${express}%`  }}>{express}%</div> 
        </div>
        <span className="progressName">MONGO DB</span>
        <div className='progress' >
        <div className='pro-fill' style={{ width: `${mongodb}%` ,backgroundColor:"green"  }}>{mongodb}%</div> 
        </div>
      </div>
      {/* right */}
      <div className="cards" >
        {/* first card */}
        <motion.div
          initial={{ left: "24rem" }}
          whileInView={{ left: "15rem" }}
          transition={transition}
          data-aos="fade-up-left"
        >
          <Card
            image={HeartEmoji}
            heading={"Design"}
            detail={"Web Designing, , Sketch, Photoshop,"}
          />
        </motion.div>
        {/* second card */}
        <motion.div
          initial={{ left: "-4rem", top: "10rem" }}
          whileInView={{ left: "1rem" }}
          transition={transition}
          data-aos="fade-up-right"
        >
          <Card
            image={Glasses}
            heading={"Frontend (UI)"}
            detail={"Html, Css, Bootstrap, JavaScript, React"}
          />
        </motion.div>
        {/* 3rd */}
        <motion.div
          initial={{ top: "18rem", left: "25rem" }}
          whileInView={{ left: "15rem" }}
          transition={transition}
          data-aos="fade-up-left"
        >
          <Card
            image={Humble}
            heading={"Backend "}
            detail={" Nodejs,  Express,  MongoDB" }
            color="rgba(252, 166, 31, 0.45)"
          />
        </motion.div>
        <div
          className="blur s-blur2"
          style={{ background: "var(--purple)" }}
        ></div>

      </div>
    </div>
  );
};

export default Services;